<template>
  <div>
    <!-- 搜索条件 -->
    <div class="search clear">
      <el-form :inline="true" size="mini">
        <el-form-item label="标题">
          <el-input placeholder="标题" clearable></el-input>
        </el-form-item>
        <el-form-item label="用户ID">
          <el-input placeholder="用户ID" clearable></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary">搜索</el-button>
          <el-button type="success" @click="addSysmessage">添加系统消息</el-button>
        </el-form-item>
      </el-form>
    </div>

    <!-- 表格 -->
    <div class="table">
      <el-table
        :data="tableData"
        size="small"
        height="100%"
        border
        style="width: 100%; height:100%;"
      >
        <el-table-column prop="userID" label="用户ID"></el-table-column>
        <el-table-column prop="userNick" label="用户昵称"></el-table-column>
        <el-table-column prop="title" label="标题"></el-table-column>
        <el-table-column label="操作" width="120">
          <template slot-scope="scope">
            <el-button
              @click.native.prevent="deleteRow(scope.$index, tableData)"
              type="text"
              size="small"
            >编辑</el-button>
            <el-button
              @click.native.prevent="deleteRow(scope.$index, tableData)"
              type="text"
              size="small"
            >删除</el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>

    <!-- 分页 -->
    <div class="pagination">
      <el-pagination background layout="prev, pager, next, total" :total="1000"></el-pagination>
    </div>

    <!-- 表单内容 -->
    <el-dialog class="dialog" :title="dialogTitle" :visible.sync="formVisible" width="40%">
      <SysmessageForm></SysmessageForm>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="dialogVisible = false">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import { tableData } from "../../../demo/user/sysmessagelist";
import SysmessageForm from "./components/SysmessageForm";
export default {
  name: "user_sysmessagelist",
  components: {
    SysmessageForm
  },
  data() {
    return {
      tableData,
      dialogTitle: "添加系统消息",
      formVisible: false
    };
  },
  methods: {
    //   添加系统消息
    addSysmessage() {
      this.formVisible = true;
    }
  }
};
</script>

<style scoped>
</style>